html, body{
    margin:0px;
    /* Read text font size */
    font-size:16px;
    font-family: 'Segoe UI', 'Open Sans', Helvetica, sans-serif;
    line-height: 1.5em;
    text-align:left;   
    position: relative;
}

/* Heading */
h1, .head-1{
    font-size: 2.5rem;
    font-weight: 300;
    line-height:1.24em;
}
h2, .head-2{
    font-size: 2.25rem;
    font-weight:300;
    line-height:1.25em;
}
h3, .head-3{
    font-size: 2.125rem;
    font-weight:300;
    line-height:1.25em;
}
h4, .head-4{
    font-size: 1.875rem;
    font-weight:300; 
    line-height:1.25em;
}
h5, .head-5{
    font-size: 1.475rem;
    font-weight:300;
    line-height:0.75em;
}
h6, .head-6{
    font-size: 1.25rem;
    font-weight:300;
    line-height:1.25em;
}
.text{
    font-size: 14px;
    font-weight: 400;
    line-height: 1.25em;
}

